.graph_container {
  width: 70vw;
  height: 70vh;
  background-color: #f0f0f0;
  position: relative;
  margin: 30px auto;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}
